import React from 'react'
import HelmetExport, { Helmet } from 'react-helmet'
import { makeStyles } from '@material-ui/core/styles'
import { lang, getTranslate } from '../localization/index'
import { Grid, Typography, Button, IconButton } from '@material-ui/core'
import Title from '../components/title'
import fullLengthImage from '../assets/imgs/fullLength.jpg'

const useStyles = makeStyles((theme) => ({
  root: {
    minHeight: '100vh',
  },
  content: {
    padding: '100px 60px 60px 60px',
    [theme.breakpoints.down('xs')]: {
      padding: '80px 10px 20px 10px',
    },
  },
  imgGrid: {
    position: 'relative',
    '&::after': {
      content: '""',
      position: 'absolute',
      left: 0,
      top: 0,
      height: '65%',
      width: '15px',
      background: 'rgba(3,127,255,0.6)',
    },
    '&::before': {
      content: '""',
      position: 'absolute',
      right: 0,
      bottom: 6,
      height: '65%',
      width: '15px',
      background: 'rgba(3,127,255,0.6)',
    },
  },
  img: {
    width: '100%',
  },
  textGrid: {
    padding: '0 0 0 30px',
    [theme.breakpoints.down('sm')]: {
      padding: '30px 0 0 0',
    },
  },
  hiName: {
    color: theme.palette.primary.main,
    [theme.breakpoints.down('xs')]: {
      display: 'block',
    },
  },
  textBody: {
    marginTop: 20,
  },
  dlBtn: {
    textDecoration: 'none',
  },
}))

export default function About() {
  const classes = useStyles()
  const bnk = getTranslate()
  return (
    <Grid container alignItems='flex-start' className={classes.root}>
      <Helmet>
        <title>
          {bnk.mahta} | {bnk.about}
        </title>
        <meta name='description' content={bnk.aboutMetaDsc} />
      </Helmet>
      <Grid item container xs={12} className={classes.content}>
        <Title text={bnk.about} />
        <Grid xs={12} md={6} className={classes.imgGrid}>
          <img className={classes.img} src={fullLengthImage} alt={bnk.name} />
        </Grid>
        <Grid xs={12} md={6} className={classes.textGrid}>
          <Typography variant='h3'>
            {bnk.hi1}
            <span className={classes.hiName}>{bnk.mahta}</span>
            {bnk.hi2}
          </Typography>
          <Typography
            className={classes.textBody}
            color='secondary'
            variant='body1'
          >
            {bnk.introduction1}
            <br />
            {bnk.introduction2}
            <br />
            {bnk.introduction3}
          </Typography>
          <div className={classes.textBody}>
            <a
              className={classes.dlBtn}
              href='https://drive.google.com/file/d/1NZrGZPaWrkirZ4Lf0HdlHN6agLqCZ0A9/view?usp=drive_link'
              download='CV_Mahta_Ahmadi.pdf'
              target='_blank'
              rel='noreferrer'
            >
              <Button variant='contained' color='primary'>
                download CV
              </Button>
            </a>
          </div>
        </Grid>
      </Grid>
    </Grid>
  )
}
