import React, { useState } from 'react'
import './index.css'
import Home from './Home'
import About from './About'
import Education from './Education'
import Publication from './Publication'
import Honors from './Honors'
import Experiences from './Experiences'
import Skills from './Skills'
import Contact from './Contact'
import Drawer from '@material-ui/core/Drawer'
import Hidden from '@material-ui/core/Hidden'
import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import MenuIcon from '@material-ui/icons/Menu'
import Typography from '@material-ui/core/Typography'
import { makeStyles } from '@material-ui/core/styles'
import profileImage from '../assets/imgs/profile.jpg'
import { lang, getTranslate, changeLanguage } from '../localization/index'
import { Button } from '@material-ui/core'
import {
  BrowserRouter as Router,
  Routes,
  Route,
  NavLink,
  useParams,
} from 'react-router-dom'

const bnk = getTranslate()
const drawerWidth = 260

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  menuButton: {
    position: 'fixed',
    zIndex: 1,
    top: 20,
    left: 0,
    right: 'auto',
    marginLeft: -1,
    width: 50,
    height: 50,
    backgroundColor: theme.palette.primary.backgroundColor,
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    },
    border: '1px solid #2e344e',
    borderRadius: 0,
  },
  mainDrawer: {
    display: 'flex',
    flexDirection: 'column',
    height: '100%',
  },
  imgDrawer: {
    width: '100%',
    padding: '20px 0',
    borderBottom: '1px solid #2e344e',
    textAlign: 'center',
  },
  profileImage: {
    maxWidth: 200,
    borderRadius: 100,
    border: '7px solid #2e344e',
  },
  menuDrawer: {
    flexGrow: 1,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
  },
  bottonDrawer: {
    display: 'flex',
    justifyContent: 'center',
    padding: 15,
    borderTop: '1px solid #2e344e',
  },
  drawerPaper: {
    width: drawerWidth,
    backgroundColor: '#191d2b',
    height: '100vh',
    borderRight: '1px solid #2e344e',
  },
  content: {
    flexGrow: 1,
    paddingRight: theme.spacing(3),
    paddingLeft: theme.spacing(3),
    position: 'relative',
  },
  listLink: {
    textDecoration: 'none',
    // backgroundColor: 'blue',
  },
  listLinkActive: {
    backgroundColor: 'red',
    textDecoration: 'underline',
  },
}))

function ResponsiveDrawer(props) {
  const { window } = props
  const { page } = useParams()
  {
    console.log(
      '************************* Mahta Ahmadi *****************************************'
    )
    console.log(page)
  }
  const classes = useStyles()
  const [mobileOpen, setMobileOpen] = useState(false)

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen)
  }

  const drawer = (
    <div className={classes.mainDrawer}>
      <div className={classes.imgDrawer}>
        <img
          src={profileImage}
          alt={bnk.name}
          className={classes.profileImage}
        />
      </div>
      <List className={classes.menuDrawer}>
        {/* Home */}
        <NavLink
          activeClassName={classes.listLinkActive}
          className={classes.listLink}
          to='/home'
        >
          <ListItem
            className='listItem'
            onClick={() => setMobileOpen(false)}
            button
          >
            <ListItemText
              disableTypography={true}
              children={
                <Typography className='listItemText' variant='subtitle2'>
                  {bnk.home}
                </Typography>
              }
            />
            <div className='overlay' />
          </ListItem>
        </NavLink>
        {/* About */}
        <NavLink
          activeClassName={classes.listLinkActive}
          className={classes.listLink}
          to='/about'
        >
          <ListItem
            className='listItem'
            style={{
              backgroundColor: page === 'about' ? '#037fff' : 'transparent',
            }}
            onClick={() => {
              setMobileOpen(false)
              console.log(
                '************************* Mahta Ahmadi *****************************************'
              )
              console.log(page)
            }}
            button
          >
            <ListItemText
              disableTypography={true}
              children={
                <Typography className='listItemText' variant='subtitle2'>
                  {bnk.about}
                </Typography>
              }
            />
            <div className='overlay' />
          </ListItem>
        </NavLink>
        {/* Education */}
        <NavLink className={classes.listLink} to='/education'>
          <ListItem
            className='listItem'
            onClick={() => setMobileOpen(false)}
            button
          >
            <ListItemText
              disableTypography={true}
              children={
                <Typography className='listItemText' variant='subtitle2'>
                  {bnk.edu}
                </Typography>
              }
            />
            <div className='overlay' />
          </ListItem>
        </NavLink>
        {/* Publication */}
        <NavLink className={classes.listLink} to='/publication'>
          <ListItem
            className='listItem'
            onClick={() => setMobileOpen(false)}
            button
          >
            <ListItemText
              disableTypography={true}
              children={
                <Typography className='listItemText' variant='subtitle2'>
                  {bnk.pub}
                </Typography>
              }
            />
            <div className='overlay' />
          </ListItem>
        </NavLink>
        {/* Honors */}
        <NavLink className={classes.listLink} to='/honors'>
          <ListItem
            className='listItem'
            onClick={() => setMobileOpen(false)}
            button
          >
            <ListItemText
              disableTypography={true}
              children={
                <Typography className='listItemText' variant='subtitle2'>
                  {bnk.honor}
                </Typography>
              }
            />
            <div className='overlay' />
          </ListItem>
        </NavLink>
        {/* Experiences */}
        <NavLink className={classes.listLink} to='/experiences'>
          <ListItem
            className='listItem'
            onClick={() => setMobileOpen(false)}
            button
          >
            <ListItemText
              disableTypography={true}
              children={
                <Typography className='listItemText' variant='subtitle2'>
                  {bnk.exp}
                </Typography>
              }
            />
            <div className='overlay' />
          </ListItem>
        </NavLink>
        {/* Skills */}
        <NavLink className={classes.listLink} to='/skills'>
          <ListItem
            className='listItem'
            onClick={() => setMobileOpen(false)}
            button
          >
            <ListItemText
              disableTypography={true}
              children={
                <Typography className='listItemText' variant='subtitle2'>
                  {bnk.skill}
                </Typography>
              }
            />
            <div className='overlay' />
          </ListItem>
        </NavLink>
        {/* Contact */}
        <NavLink className={classes.listLink} to='/contact'>
          <ListItem
            className='listItem'
            onClick={() => setMobileOpen(false)}
            button
          >
            <ListItemText
              disableTypography={true}
              children={
                <Typography className='listItemText' variant='subtitle2'>
                  {bnk.contact}
                </Typography>
              }
            />
            <div className='overlay' />
          </ListItem>
        </NavLink>
      </List>
    </div>
  )

  const container =
    window !== undefined ? () => window().document.body : undefined

  return (
    <Router>
      <div className={classes.root}>
        <IconButton
          color='inherit'
          aria-label='open drawer'
          edge='start'
          onClick={handleDrawerToggle}
          className={classes.menuButton}
        >
          <MenuIcon />
        </IconButton>
        <nav className={classes.drawer} aria-label='mailbox folders'>
          {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
          <Hidden smUp implementation='css'>
            <Drawer
              container={container}
              variant='temporary'
              open={mobileOpen}
              onClose={handleDrawerToggle}
              classes={{
                paper: classes.drawerPaper,
              }}
              ModalProps={{
                keepMounted: true, // Better open performance on mobile.
              }}
            >
              {drawer}
            </Drawer>
          </Hidden>
          <Hidden xsDown implementation='css'>
            <Drawer
              classes={{
                paper: classes.drawerPaper,
              }}
              variant='permanent'
              open
            >
              {drawer}
            </Drawer>
          </Hidden>
        </nav>

        <main className={classes.content}>
          <span className='line line1' />
          <span className='line line2' />
          <span className='line line3' />
          <span className='line line4' />
          <Routes>
            <Route exact path='/' element={<Home />} />
            <Route path='/about' element={<About />} />
            <Route path='/education' element={<Education />} />
            <Route path='/publication' element={<Publication />} />
            <Route path='/honors' element={<Honors />} />
            <Route path='/experiences' element={<Experiences />} />
            <Route path='/skills' element={<Skills />} />
            <Route path='/contact' element={<Contact />} />
            <Route path='*' element={<Home />} />
          </Routes>
        </main>
      </div>
    </Router>
  )
}

export default ResponsiveDrawer
