import { createTheme } from '@material-ui/core/styles'
import palette from './palette'
import { lang, getDirection, getFont } from '../localization/index'

let theme = createTheme()
theme = createTheme({
  palette: palette,
  spacing: 2.5,
  direction: getDirection(),
  typography: {
    h1: {
      fontFamily: getFont(),
      fontWeight: 700,
      fontSize: lang === 'en' ? 52 : 50,
      textAlign: 'center',
      [theme.breakpoints.down('xs')]: {
        fontSize: lang === 'en' ? 42 : 40,
      },
    },
    h2: {
      fontFamily: getFont(),
      fontWeight: 700,
      fontSize: 40,
      textAlign: 'left',
      textTransform: 'uppercase',
      [theme.breakpoints.down('xs')]: {
        fontSize: 34,
      },
    },
    h3: {
      fontFamily: getFont(),
      fontWeight: 600,
      fontSize: lang === 'en' ? 32 : 28,
      textAlign: 'left',
      [theme.breakpoints.down('xs')]: {
        fontSize: lang === 'en' ? 30 : 27,
      },
    },
    h4: {
      fontFamily: getFont(),
      fontWeight: 600,
      fontSize: lang === 'en' ? 28 : 26,
      [theme.breakpoints.down('xs')]: {
        fontSize: lang === 'en' ? 24 : 22,
      },
    },
    h5: {
      fontFamily: getFont(),
      fontWeight: 600,
      fontSize: lang === 'en' ? 23 : 21,
      [theme.breakpoints.down('xs')]: {
        fontSize: lang === 'en' ? 20 : 18,
      },
    },
    h6: {
      fontFamily: getFont(),
      fontWeight: 600,
      fontSize: lang === 'en' ? 18 : 16,
      [theme.breakpoints.down('xs')]: {
        fontSize: lang === 'en' ? 16 : 14,
      },
    },
    body1: {
      fontFamily: getFont(),
      fontWeight: 400,
      fontSize: lang === 'en' ? 18 : 16,
      lineHeight: 1.5,
      textAlign: 'justify',
      textDecoration: 'none',
      [theme.breakpoints.down('xs')]: {
        fontSize: lang === 'en' ? 17 : 15,
      },
    },
    subtitle1: {
      fontFamily: getFont(),
      fontWeight: 400,
      fontSize: lang === 'en' ? 20 : 18,
      textAlign: 'center',
      [theme.breakpoints.down('xs')]: {
        fontSize: lang === 'en' ? 18 : 16,
      },
    },
    subtitle2: {
      fontFamily: getFont(),
      fontWeight: 600,
      fontStyle: 'normal',
      fontSize: 15,
      letterSpacing: lang === 'en' ? 2 : 0,
      textTransform: 'uppercase',
      textAlign: 'center',
      [theme.breakpoints.down('xs')]: {
        fontSize: 14,
      },
    },
    button: {
      fontFamily: getFont(),
      fontWeight: 400,
      fontSize: lang === 'en' ? 14 : 12,
      letterSpacing: lang === 'en' ? 2 : 0,
    },
  },
})

export default theme