import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import { Grid, Typography } from '@material-ui/core'

export default function EduBox({ title, time, uni, dsc, gpa, href }) {
  const useStyles = makeStyles((theme) => ({
    root: {
      width: '100%',
      marginTop: 20,
    },
    innerGrid: {
      padding: '15px 120px 0px 50px',
      marginBottom: 30,
    },
    textBody: {
      marginTop: 10,
      minWidth: '100%',
    },
    content: {
      color: theme.palette.secondary.main,
      textDecoration: 'none',
      transition: '.5s',
      '&:hover': {
        color: theme.palette.primary.main,
        transition: '.5s',
      },
    },
  }))
  const classes = useStyles()

  return (
    <div className={classes.root}>
      <Grid item container xs={12} className={classes.innerGrid}>
        <Typography className={classes.textBody} color='secondary' variant='h4'>
          {title}
        </Typography>
        <Typography
          className={classes.textBody}
          color='secondary'
          variant='textBody'
        >
          {time}
        </Typography>
        <a
          href={href}
          target='_blank'
          rel='noreferrer'
          className={classes.content}
        >
          <Typography className={classes.textBody} variant='h6'>
            {uni}
          </Typography>
        </a>
        <Typography className={classes.textBody} color='primary' variant='h6'>
          {dsc}
        </Typography>
        <Typography
          className={classes.textBody}
          color='secondary'
          variant='textBody'
        >
          {gpa}
        </Typography>
      </Grid>
    </div>
  )
}
