import React from 'react';
import { makeStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import StepConnector from '@material-ui/core/StepConnector';
import Typography from '@material-ui/core/Typography';
import Hidden from '@material-ui/core/Hidden';
import { getTranslate } from '../localization/index';

const useStyles = makeStyles(theme => ({
    stepper: {
        background: 'transparent',
    },
    stepConnector: {
        padding: 0,
        '& span': {
            borderLeft: '3px solid #2e344e',
        },
    },
    stepLabelLabel: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        [theme.breakpoints.down('md')]:{
            flexDirection: 'column',
            alignItems: 'flex-start',
        }
    },
    icon: {
        width: 15,
        height: 15,
        border: '3px solid #2e344e',
        borderRadius: '100%',
        marginLeft: 5,
    },
    date: {
        minWidth: 200,
    },
    dash: {
        width: 30,
        height: 1,
        background: '#2e344e',
        marginRight: 10,
    },
    stepContent: {
        borderLeft: '3px solid #2e344e',
        marginTop: 0,
        padding: '8px 8px 0px 255px',
        [theme.breakpoints.down('md')]:{
            paddingLeft: 15,
    },
},
}))


export default function HonorStepper() {
    const classes = useStyles()
    const bnk = getTranslate()
// ?
const steps = [
  { id: 0, date: '', title: '', subtitle: '', content: '' },
  {
    id: 1,
    date: bnk.honor4y,
    title: bnk.honor4,
    subtitle: '',
    content: bnk.honor4d,
  },
  {
    id: 2,
    date: bnk.honor3y,
    title: bnk.honor3,
    subtitle: '',
    content: bnk.honor3d,
  },
  {
    id: 3,
    date: bnk.honor2y,
    title: bnk.honor2,
    subtitle: '',
    content: bnk.honor2d,
  },
  {
    id: 4,
    date: bnk.honor1y,
    title: bnk.honor1,
    subtitle: '',
    content: bnk.honor1d,
  },
  { id: -1, date: '', title: '', subtitle: '', content: '' },
]


    return (
        <Grid container>
            <Stepper orientation="vertical" connector={<StepConnector className={classes.stepConnector} />} className={classes.stepper}>
                {steps.map((step) => step.id > 0 ? (
                    <Step key={step.id} active={true}>
                        <StepLabel classes={{ label: classes.stepLabelLabel }}
                            icon={<span className={classes.icon} />} className={classes.stepLabel}>
                            <Typography variant='h6' color='secondary' className={classes.date}>{step.date}</Typography>
                            <Hidden mdDown>
                                <span className={classes.dash}></span>
                            </Hidden>
                            <Typography variant='h5' color='primary'>{step.title}</Typography>
                        </StepLabel>
                        <StepContent className={classes.stepContent}>
                            <Typography variant='h6'>{step.subtitle}</Typography>
                            <Typography variant='body1' color='secondary'>{step.content}</Typography>
                        </StepContent>
                    </Step>
                ) : step.id === 0 ? (<Step></Step>) : (null))}
            </Stepper>
        </Grid>
    );
}