import React from 'react'
import { Helmet } from 'react-helmet'
import { makeStyles } from '@material-ui/core/styles'
import { getTranslate } from '../localization/index'
import { Grid, Typography } from '@material-ui/core'
import Title from '../components/title'
import SkillBox from '../components/skillBox'
import LanguageIcon from '@material-ui/icons/Language'
import LaptopIcon from '@material-ui/icons/Laptop'
import KeyboardSharpIcon from '@material-ui/icons/KeyboardSharp'
import MouseIcon from '@material-ui/icons/Mouse'
import CodeSharpIcon from '@material-ui/icons/CodeSharp'
import ColorLensIcon from '@material-ui/icons/ColorLens'

const useStyles = makeStyles((theme) => ({
  root: {
    minHeight: '100vh',
  },
  contentTop: {
    padding: '100px 60px 0px 60px',
    [theme.breakpoints.down('xs')]: {
      padding: '80px 10px 0px 10px',
    },
  },
  contentBottom: {
    padding: '100px 60px 60px 60px',
    [theme.breakpoints.down('xs')]: {
      padding: '80px 10px 20px 10px',
    },
  },
  icon: {
    color: theme.palette.secondary.main,
    fontSize: 35,
    marginRight: 10,
  },
  gridStepper: {
    margin: '15px 0px',
  },
  servicesGrid: {
    marginTop: 40,
  },
  serviceIcon: {
    fontSize: 46,
    color: theme.palette.primary.main,
  },
  innerGrid: {
    padding: '0px 15px 0px 10px',
    marginBottom: 50,
  },
  listItem: {
    marginBottom: 10,
  },
  listItemTitle: {
    minWidth: 110,
    [theme.breakpoints.down('xs')]: {
      minWidth: 75,
    },
    display: 'inline-block',
    color: '#037fff',
  },
}))

export default function Skills() {
  const classes = useStyles()
  const bnk = getTranslate()
  return (
    <Grid container alignItems='flex-start' className={classes.root}>
      <Helmet>
        <title>
          {bnk.name} | {bnk.skill}
        </title>
        <meta name='description' content={bnk.skillMetaDsc} />
      </Helmet>
      <Grid item container xs={12} className={classes.contentTop}>
        <Title text={bnk.skill} />
        {/*---------------------------------------------------------------- LANGUAGES */}
        <Grid container direction='row' alignContent='center'>
          <LanguageIcon className={classes.icon} />
          <Typography variant='h4'>{bnk.lang}</Typography>
        </Grid>
        <Grid item container xs={12} className={classes.innerGrid}>
          <div className={classes.textBody}>
            <Typography
              className={classes.listItem}
              color='secondary'
              variant='h5'
            >
              <ul>
                <li>
                  <b className={classes.listItemTitle}>{bnk.lang1}</b>:{' '}
                  {bnk.lang1dsc}
                </li>
                <li>
                  <b className={classes.listItemTitle}>{bnk.lang2}</b>:{' '}
                  {bnk.lang2dsc}
                </li>
              </ul>
            </Typography>
          </div>
        </Grid>
        {/* ---------------------------------------------------------- COMPUTER SKILLS */}
        <Grid container direction='row' alignContent='center'>
          <LaptopIcon className={classes.icon} />
          <Typography variant='h4'>{bnk.comSkills}</Typography>
        </Grid>
        <Grid container>
          <SkillBox
            icon={<KeyboardSharpIcon className={classes.serviceIcon} />}
            title={bnk.skillA}
            content1={bnk.skillA1}
            content2={bnk.skillA2}
            content3={bnk.skillA3}
            content4=''
          />
          <SkillBox
            icon={<MouseIcon className={classes.serviceIcon} />}
            title={bnk.skillB}
            content1={bnk.skillB1}
            content2={bnk.skillB2}
            content3={bnk.skillB3}
            content4={bnk.skillB4}
          />
          <SkillBox
            icon={<CodeSharpIcon className={classes.serviceIcon} />}
            title={bnk.skillC}
            content1={bnk.skillC1}
            content2={bnk.skillC2}
            content3={bnk.skillC3}
            content4={bnk.skillC4}
          />
        </Grid>
        {/* ------------------------------------------------------------ INTERESTS */}
        <Grid item container xs={12} className={classes.innerGrid}>
        </Grid>
        <Grid container direction='row' alignContent='center'>
          <ColorLensIcon className={classes.icon} />
          <Typography variant='h4'>{bnk.ints}</Typography>
        </Grid>
        <Grid item container xs={12} className={classes.innerGrid}>
          <div className={classes.textBody}>
            <Typography
              className={classes.listItem}
              color='secondary'
              variant='h5'
            >
              <ul>
                <li>
                  <b className={classes.listItemTitle}>{bnk.int1}</b>
                </li>
                <li>
                  <b className={classes.listItemTitle}>{bnk.int2}</b>
                </li>
                <li>
                  <b className={classes.listItemTitle}>{bnk.int3}</b>
                </li>
                <li>
                  <b className={classes.listItemTitle}>{bnk.int4}</b>
                </li>
              </ul>
            </Typography>
          </div>
        </Grid>
      </Grid>
    </Grid>
  )
}
