import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import { Grid, Typography, Button } from '@material-ui/core'

export default function PubBox({
  author1,
  authors,
  hrefPDF,
  hrefGS,
  hrefIEEE,
  title,
  journalTime,
}) {
  const useStyles = makeStyles((theme) => ({
    root: {
      width: '100%',
      marginTop: 20,
    },
    innerGrid: {
      padding: '0px 120px 0px 50px',
      marginBottom: 5,
    },
    textBody: {
      marginTop: 5,
      marginLeft: 25,
    },
    dlBtn: {
      textDecoration: 'none',
    },
    content: {
      color: theme.palette.primary.main,
      // color: theme.palette.secondary.main,
      textDecoration: 'none',
      transition: '.5s',
      '&:hover': {
        color: theme.palette.text.primary,
        // color: theme.palette.primary.main,
        transition: '.5s',
      },
    },
  }))
  const classes = useStyles()

  return (
    <div className={classes.root}>
      <Grid item container xs={12} className={classes.innerGrid}>
        <Typography className={classes.textBody} variant='h5'>
          {author1}
        </Typography>
        <Typography className={classes.textBody} color='secondary' variant='h5'>
          {authors}
        </Typography>
        <Typography className={classes.textBody} color='primary' variant='h5'>
          {title}
        </Typography>
        <Typography className={classes.textBody} color='secondary' variant='h5'>
          {journalTime}
        </Typography>
      </Grid>
      <Grid item container xs={12} className={classes.innerGrid}>
        <div className={classes.textBody}>
          <a
            className={classes.dlBtn}
            href={hrefPDF}
            download='Paper_Mahta_Ahmadi.pdf'
            target='_blank'
            rel='noreferrer'
          >
            <Button variant='contained' color='primary'>
              pdf
            </Button>
          </a>
        </div>
        <div className={classes.textBody}>
          <a
            className={classes.dlBtn}
            href={hrefGS}
            target='_blank'
            rel='noreferrer'
          >
            <Button variant='contained' color='primary'>
              gs
            </Button>
          </a>
        </div>
        <div className={classes.textBody}>
          <a
            className={classes.dlBtn}
            href={hrefIEEE}
            target='_blank'
            rel='noreferrer'
          >
            <Button variant='contained' color='primary'>
              ieee
            </Button>
          </a>
        </div>
      </Grid>
    </div>
  )
}
