import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import Stepper from '@material-ui/core/Stepper'
import Step from '@material-ui/core/Step'
import StepLabel from '@material-ui/core/StepLabel'
import StepContent from '@material-ui/core/StepContent'
import StepConnector from '@material-ui/core/StepConnector'
import Typography from '@material-ui/core/Typography'
import Hidden from '@material-ui/core/Hidden'
import { getTranslate } from '../localization/index'

const useStyles = makeStyles((theme) => ({
  stepper: {
    background: 'transparent',
  },
  stepConnector: {
    padding: 0,
    '& span': {
      borderLeft: '3px solid #2e344e',
    },
  },
  stepLabelLabel: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    [theme.breakpoints.down('md')]: {
      flexDirection: 'column',
      alignItems: 'flex-start',
    },
  },
  icon: {
    width: 15,
    height: 15,
    border: '3px solid #2e344e',
    borderRadius: '100%',
    marginLeft: 5,
  },
  date: {
    minWidth: 200,
  },
  dash: {
    width: 30,
    height: 1,
    background: '#2e344e',
    marginRight: 10,
  },
  stepContent: {
    borderLeft: '3px solid #2e344e',
    marginTop: 0,
    padding: '8px 8px 0px 255px',
    [theme.breakpoints.down('md')]: {
      paddingLeft: 15,
    },
    liBullet: {
      fontWeight: 'bold',
      fontSize: 24,
    },
  },
}))

export default function WorkExpStepper() {
  const classes = useStyles()
  const bnk = getTranslate()
  // ?
  const steps = [
    { id: 0, date: '', title: '', subtitle: '', content: '' },
    {
      id: 1,
      date: bnk.w4time,
      where: bnk.w4city,
      title: bnk.w4title,
      subtitle: bnk.w4comp,
      content1: bnk.w4duty1,
      content2: bnk.w4duty2,
      content3: bnk.w4duty3,
    },
    {
      id: 2,
      date: bnk.w3time,
      where: bnk.w3city,
      title: bnk.w3title,
      subtitle: bnk.w3comp,
      content1: bnk.w3duty1,
    },
    {
      id: 3,
      date: bnk.w2time,
      where: bnk.w2city,
      title: bnk.w2title,
      subtitle: bnk.w2comp,
      content1: bnk.w2duty1,
    },
    {
      id: 4,
      date: bnk.w1time,
      where: bnk.w1city,
      title: bnk.w1title,
      subtitle: bnk.w1comp,
      content1: bnk.w1duty1,
      content2: bnk.w1duty2,
      content3: bnk.w1duty3,
    },
    { id: -1, date: '', title: '', subtitle: '', content: '' },
  ]

  return (
    <Grid container>
      <Stepper
        orientation='vertical'
        connector={<StepConnector className={classes.stepConnector} />}
        className={classes.stepper}
      >
        {steps.map((step) =>
          step.id > 0 ? (
            <Step key={step.id} active={true}>
              <StepLabel
                classes={{ label: classes.stepLabelLabel }}
                icon={<span className={classes.icon} />}
                className={classes.stepLabel}
              >
                <Typography
                  variant='h6'
                  color='secondary'
                  className={classes.date}
                >
                  {step.date}
                  <br />
                  {step.where}
                </Typography>
                <Hidden mdDown>
                  <span className={classes.dash}></span>
                </Hidden>
                <Typography variant='h5' color='primary'>
                  {step.title}
                </Typography>
              </StepLabel>
              <StepContent className={classes.stepContent}>
                <Typography variant='h6'>{step.subtitle}</Typography>
                <Typography variant='body1' color='secondary'>
                  {step.content1}
                  <br />
                  {step.content2}
                  <br />
                  {step.content3}
                </Typography>
              </StepContent>
            </Step>
          ) : step.id === 0 ? (
            <Step></Step>
          ) : null
        )}
      </Stepper>
    </Grid>
  )
}
