import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import { Typography } from '@material-ui/core'

const useStyles = makeStyles(theme => ({
    root: {
        position: 'relative',
        width: '100%',
        marginBottom: 60,
    },
    title: {
        paddingBottom:15,
        maxHeight:110,
        '&::after': {
            content: '""',
            position: 'absolute',
            left: 0,
            top: 'auto',
            bottom: 0,
            height: 5,
            borderRadius: 100,
            width: 35,
            background: '#037fff',
        },
        '&::before': {
            content: '""',
            position: 'absolute',
            left: 0,
            top: 'auto',
            bottom: 0,
            height: 5,
            borderRadius: 100,
            width: 100,
            background: 'rgba(3,127,255,0.3)',
        }
    },
    subTitle:{
        fontSize:'4.5rem',
        position:'absolute',
        left:0,
        top:'80%',
        lineHeight:'3.5rem',
        fontWeight:900,
        color:'rgba(25,29,43,.44)',
        display:'inline-block',
        zIndex:-1,
        [theme.breakpoints.down('xs')]:{
            width:320,
            overflow:'hidden',
            textOverflow:'ellipsis',
        },
    }
}))

export default function Title({text}) {
    const classes = useStyles()
    return (
        <div className={classes.root}>
            <Typography className={classes.title} variant='h2'>{text}</Typography>
            <Typography className={classes.subTitle} variant='h2'>{text}</Typography>
        </div>
    )
}
