import React from 'react'
import HelmetExport, { Helmet } from 'react-helmet'
import { makeStyles } from '@material-ui/core/styles'
import { getTranslate } from '../localization/index'
import { Grid, Typography } from '@material-ui/core'
import EduBox from '../components/eduBox'
import ThesisBox from '../components/thesisBox'
import SchoolIcon from '@material-ui/icons/School'
import SearchIcon from '@material-ui/icons/Search'
import GradeIcon from '@material-ui/icons/Grade'
import Title from '../components/title'

const useStyles = makeStyles((theme) => ({
  root: {
    minHeight: '100vh',
  },
  content: {
    padding: '100px 60px 60px 60px',
    [theme.breakpoints.down('xs')]: {
      padding: '80px 10px 20px 10px',
    },
  },
  textBody: {
    marginTop: 10,
    minWidth: '100%',
  },
  servicesGrid: {
    marginBottom: 30,
  },
  innerGrid: {
    padding: '15px 120px 0px 50px',
    marginBottom: 30,
  },
  listItem: {
    marginBottom: 10,
  },
  listItemTitle: {
    minWidth: 280,
    [theme.breakpoints.down('xs')]: {
      minWidth: 75,
    },
    display: 'inline-block',
  },
  icon: {
    color: theme.palette.secondary.main,
    fontSize: 35,
    marginRight: 10,
  },
}))

export default function Edu() {
  const classes = useStyles()
  const bnk = getTranslate()
  return (
    <Grid container alignItems='flex-start' className={classes.root}>
      <Helmet>
        <title>
          {bnk.mahta} | {bnk.edu}
        </title>
        <meta name='description' content={bnk.eduMetaDsc} />
      </Helmet>
      <Grid item container xs={12} className={classes.content}>
        {/* ------------------------------------------------------- education */}
        <Grid xs={12} className={classes.servicesGrid}>
          <Title text={bnk.edu} />
          <Grid container direction='row' alignContent='center'>
            <SchoolIcon className={classes.icon} />
            <Typography variant='h4'>{bnk.edu}</Typography>
          </Grid>
          <EduBox
            title={bnk.mscTitle}
            time={bnk.mscTime}
            uni={bnk.sut}
            dsc={bnk.mscDsc}
            gpa={bnk.mscGPA}
            href='https://en.sharif.ir'
          />
          <EduBox
            title={bnk.bscTitle}
            time={bnk.bscTime}
            uni={bnk.ui}
            dsc={bnk.bscDsc}
            gpa={bnk.bscGPA}
            href='https://www.ui.ac.ir/en'
          />
        </Grid>
        {/* ---------------------------------------------------------- thesis */}
        <Grid xs={12} className={classes.servicesGrid}>
          <Grid container direction='row' alignContent='center'>
            <SearchIcon className={classes.icon} />
            <Typography variant='h4'>{bnk.thesis}</Typography>
          </Grid>
          <ThesisBox
            title={bnk.thesisMscTitle}
            hrefSup='http://sina.sharif.ac.ir/~pakravan/'
            sup={bnk.thesisMscSup}
            hrefAdv='https://ee.sharif.edu/~mohammad.hadi/'
            adv={bnk.thesisMscAdv}
            dsc={bnk.thesisMscDsc}
          />
          <ThesisBox
            title={bnk.thesisBscTitle}
            hrefSup='https://engold.ui.ac.ir/~f.parvaresh/px/'
            sup={bnk.thesisBscSup}
            dsc={bnk.thesisBscDsc}
          />
        </Grid>
        {/* ------------------------------------------------------- selCourses */}
        <Grid xs={12} className={classes.servicesGrid}>
          <Grid container direction='row' alignContent='center'>
            <GradeIcon className={classes.icon} />
            <Typography variant='h4'>{bnk.selCourses}</Typography>
          </Grid>
          <Grid item container xs={12} className={classes.innerGrid}>
            <div className={classes.textBody}>
              <Typography
                className={classes.listItem}
                color='secondary'
                variant='body1'
              >
                <b className={classes.listItemTitle}>{bnk.course1}</b>:{' '}
                {bnk.grade1}
              </Typography>
              <Typography
                className={classes.listItem}
                color='secondary'
                variant='body1'
              >
                <b className={classes.listItemTitle}>{bnk.course2}</b>:{' '}
                {bnk.grade2}
              </Typography>
              <Typography
                className={classes.listItem}
                color='secondary'
                variant='body1'
              >
                <b className={classes.listItemTitle}>{bnk.course3}</b>:{' '}
                {bnk.grade3}
              </Typography>
              <Typography
                className={classes.listItem}
                color='secondary'
                variant='body1'
              >
                <b className={classes.listItemTitle}>{bnk.course4}</b>:{' '}
                {bnk.grade4}
              </Typography>
              <Typography
                className={classes.listItem}
                color='secondary'
                variant='body1'
              >
                <b className={classes.listItemTitle}>{bnk.course5}</b>:{' '}
                {bnk.grade5}
              </Typography>
              <Typography
                className={classes.listItem}
                color='secondary'
                variant='body1'
              >
                <b className={classes.listItemTitle}>{bnk.course6}</b>:{' '}
                {bnk.grade6}
              </Typography>
              <Typography
                className={classes.listItem}
                color='secondary'
                variant='body1'
              >
                <b className={classes.listItemTitle}>{bnk.course7}</b>:{' '}
                {bnk.grade7}
              </Typography>
              <Typography
                className={classes.listItem}
                color='secondary'
                variant='body1'
              >
                <b className={classes.listItemTitle}>{bnk.course8}</b>:{' '}
                {bnk.grade8}
              </Typography>
            </div>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}
