import React from 'react'
import { Helmet } from 'react-helmet'
import { makeStyles } from '@material-ui/core/styles'
import { getTranslate } from '../localization/index'
import { Grid, Typography } from '@material-ui/core'
import Title from '../components/title'
import WorkExpStepper from '../components/workExpStepper'
import Service from '../components/service'
import SchoolIcon from '@material-ui/icons/School'
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter'
import MemoryIcon from '@material-ui/icons/Memory'
import LaptopWindowsIcon from '@material-ui/icons/LaptopWindows'
import WifiIcon from '@material-ui/icons/Wifi'
import AppsIcon from '@material-ui/icons/Apps'
import CodeSharpIcon from '@material-ui/icons/CodeSharp'
import EmojiObjectsIcon from '@material-ui/icons/EmojiObjects'
import PublicIcon from '@material-ui/icons/Public'
import HelpIcon from '@material-ui/icons/Help'

const useStyles = makeStyles((theme) => ({
  root: {
    minHeight: '100vh',
  },
  contentTop: {
    padding: '100px 60px 0px 60px',
    [theme.breakpoints.down('xs')]: {
      padding: '80px 10px 0px 10px',
    },
  },
  contentBottom: {
    padding: '100px 60px 60px 60px',
    [theme.breakpoints.down('xs')]: {
      padding: '80px 10px 20px 10px',
    },
  },
  icon: {
    color: theme.palette.secondary.main,
    fontSize: 35,
    marginRight: 10,
  },
  gridStepper: {
    margin: '15px 0px',
  },
  servicesGrid: {
    marginTop: 40,
  },
  serviceIcon: {
    fontSize: 46,
    color: theme.palette.primary.main,
  },
}))

export default function Experiences() {
  const classes = useStyles()
  const bnk = getTranslate()
  return (
    <Grid container alignItems='flex-start' className={classes.root}>
      <Helmet>
        <title>
          {bnk.name} | {bnk.exp}
        </title>
        <meta name='description' content={bnk.expMetaDsc} />
      </Helmet>
      <Grid item container xs={12} className={classes.contentTop}>
        <Title text={bnk.exp} />
        <Grid container direction='row' alignContent='center'>
          <SchoolIcon className={classes.icon} />
          <Typography variant='h4'>{bnk.tExp}</Typography>
        </Grid>
        <Grid container>
          <Service
            icon={<MemoryIcon className={classes.serviceIcon} />}
            title={bnk.tExp8}
            content1={bnk.tExp8dsc}
            content2={bnk.tExp8time}
          />
          <Service
            icon={<LaptopWindowsIcon className={classes.serviceIcon} />}
            title={bnk.tExp7}
            content1={bnk.tExp7dsc}
            content2={bnk.tExp7time}
          />
          <Service
            icon={<WifiIcon className={classes.serviceIcon} />}
            title={bnk.tExp6}
            content1={bnk.tExp6dsc}
            content2={bnk.tExp6time}
          />
          <Service
            icon={<AppsIcon className={classes.serviceIcon} />}
            title={bnk.tExp5}
            content1={bnk.tExp5dsc}
            content2={bnk.tExp5time}
          />
          <Service
            icon={<EmojiObjectsIcon className={classes.serviceIcon} />}
            title={bnk.tExp4}
            content1={bnk.tExp4dsc}
            content2={bnk.tExp4time}
          />
          <Service
            icon={<CodeSharpIcon className={classes.serviceIcon} />}
            title={bnk.tExp3}
            content1={bnk.tExp3dsc}
            content2={bnk.tExp3time}
          />
          <Service
            icon={<PublicIcon className={classes.serviceIcon} />}
            title={bnk.tExp2}
            content1={bnk.tExp2dsc}
            content2={bnk.tExp2time}
          />
          <Service
            icon={<HelpIcon className={classes.serviceIcon} />}
            title={bnk.tExp1}
            content1={bnk.tExp1dsc}
            content2={bnk.tExp1time}
          />
        </Grid>
      </Grid>
      <Grid item container xs={12} className={classes.contentBottom}>
        <Grid container direction='column'>
          <Grid container direction='row' alignContent='center'>
            <BusinessCenterIcon className={classes.icon} />
            <Typography variant='h4'>{bnk.wExp}</Typography>
          </Grid>
          <Grid container className={classes.gridStepper}>
            <WorkExpStepper />
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}
