import React from 'react'
import { Helmet } from 'react-helmet'
import { makeStyles } from '@material-ui/core/styles'
import { getTranslate } from '../localization/index'
import { Grid, Typography } from '@material-ui/core'
import Title from '../components/title'
import HonorStepper from '../components/honorStepper'

const useStyles = makeStyles((theme) => ({
  root: {
    minHeight: '100vh',
  },
  contentTop: {
    padding: '100px 60px 0px 60px',
    [theme.breakpoints.down('xs')]: {
      padding: '80px 10px 0px 10px',
    },
  },
  contentBottom: {
    padding: '100px 60px 60px 60px',
    [theme.breakpoints.down('xs')]: {
      padding: '80px 10px 20px 10px',
    },
  },
  icon: {
    color: theme.palette.secondary.main,
    fontSize: 35,
    marginRight: 10,
  },
  gridStepper: {
    margin: '15px 0px',
  },
  servicesGrid: {
    marginTop: 40,
  },
  serviceIcon: {
    fontSize: 46,
    color: theme.palette.primary.main,
  },
}))

export default function Honors() {
  const classes = useStyles()
  const bnk = getTranslate()
  return (
    <Grid container alignItems='flex-start' className={classes.root}>
      <Helmet>
        <title>
          {bnk.name} | {bnk.honor}
        </title>
        <meta name='description' content={bnk.honorMetaDsc} />
      </Helmet>
      <Grid item container xs={12} className={classes.contentBottom}>
        <Title text={bnk.honor} />
        <Grid container direction='column'>
          <Grid container className={classes.gridStepper}>
            <HonorStepper />
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  )
}
